import { Component, OnInit } from '@angular/core';
import { RestService } from '../provider/rest.service';
import { NavController, AlertController } from '@ionic/angular';

declare var cordova;

@Component({
  selector: 'app-api-code',
  templateUrl: './api-code.page.html',
  styleUrls: ['./api-code.page.scss'],
})
export class ApiCodePage implements OnInit {
  apicode: any;
  code: any = '';//HY012020 //BT002020
  urlData: any;
  constructor(public navCtrl: NavController, private RestService: RestService, private alertCtrl: AlertController) { }

  ngOnInit() {
  }

  ionViewDidEnter() {
    //this.getApiUrl();
  }


  async getApiUrl() {
    // localStorage.setItem('modeType', 'Offline');
    // localStorage.setItem('CompanyUrl', 'http://hypercom.bthrust.com/');
    // this.navCtrl.navigateRoot("/login");
    // return;
    //this.code = 'BT002020';
    if (this.code == '') {
      const alert = await this.alertCtrl.create({
        cssClass: 'cstmAlert',
        message: 'Please enter code.',
        buttons: ['OK']
      });
      await alert.present();
      return;
    }
    this.RestService.requestViaGet('GenicProfile/GenicClients/' + this.code, '').then(async (response) => {
      console.log(response);
      this.apicode = response;
      if (this.apicode.Message == "success") {
        this.urlData = this.apicode.result;
        console.log(this.urlData);
        var mode;
        if (this.urlData.IsOffline) {
          mode = 'Offline'
        } else {
          mode = 'Online'
        }
        localStorage.setItem('modeType', mode);
        localStorage.setItem('CompanyUrl', this.urlData.CompanyUrl);
        // this.startLocation();
        this.navCtrl.navigateRoot("/login");
      } else {
        const alert = await this.alertCtrl.create({
          cssClass: 'cstmAlert',
          message: this.apicode.MessageDescription,
          buttons: ['OK']
        });
        await alert.present();
      }
    });
  }


  startLocation() {
    alert('start loca')
    cordova.exec(null, null, "LocationViewController", "getLatLong", []);
  }
}
